declare var $: any
import { Component } from '@angular/core';
import { AndroidPermissions } from '@ionic-native/android-permissions';
import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { IonicPage, LoadingController, MenuController, NavController, NavParams, Platform, ToastController } from 'ionic-angular';
import { ConfigProvider } from '../../../providers/config';
import { HttpServicesProvider } from '../../../providers/http-services';
import { PhoneDeviceProvider } from '../../../providers/phone-device';
import { StorageProvider } from '../../../providers/storage';
declare var fuzzySearch:any 

/**
 * Generated class for the LmpjifilesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lmpjifiles',
  templateUrl: 'lmpjifiles.html',
})
export class LmpjifilesPage {

  public list = [] // 列表
  // public lmtagStatusList = []
  public projectid = ''
  public filepath = '';
  public index = 0;
  public currentTab = 0;
  public search = '';
  public status = 'MY';
  public timer = null
  // public archiveid = null
  public page = 1;
  public maxPage = 1;
  public lmfilesStatusList = [{ name: '我的', value: 'MY' },{ name: '已就绪', value: 'COMP' },{ name: '未就绪', value: 'UNCOMP' }]

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public httpService: HttpServicesProvider,
    public phoneDevice: PhoneDeviceProvider,
    public platform: Platform,
    public toastCtrl: ToastController,
    public permission: AndroidPermissions,
    public file: File,
    public transfer: FileTransfer,
    public fileopen: FileOpener,
    public loadingCtrl: LoadingController,
    public configProvider: ConfigProvider,
    public menuCtrl: MenuController,
    public storage: StorageProvider) {
    this.phoneDevice.phoneBacktoPop(this.navCtrl);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LmtagarchivemanagePage');
  }
  ionViewWillEnter() { //当将要进入页面时触发
    // this.list = [];
    // this.lmtagStatusList = [];
    // this.initproject();
    this.projectid = this.storage.get(StorageProvider.LM_PROJECTID);
    // this.getDict();
    if (this.projectid) {
      this.page = 1
      this.initlist();
      // this.initTree();
    }
  }

  doRefresh(refresher) {
    // this.initproject()
    // this.getDict();
    this.initlist();
    setTimeout(() => {
      refresher.complete(); //停止下拉刷新
    }, 1500)
  }

  doInfinite(infiniteScroll){ //上拉加载
    if ((this.page + 1) <= this.maxPage) {
      this.page ++
      this.initlist()
    }
  }

  changeTab(status:string, i) {
    this.currentTab = i
    this.status = status
    this.initlist()
  }

  // 搜索+防抖
  handleSearch() {
    // console.log(this.search);
    clearTimeout(this.timer)
    this.timer = setTimeout(() => {
      this.initlist()
      this.timer = null
    }, 300)
  }

  // handleInfo(item) {
  //   this.navCtrl.push('LmtagstartupreportinfoPage', { item })
  // }

  initlist() {
    this.list = []

    let url = `/lmtagProject/list?techprogram=1&sort=createtime&order=desc&projectid=${this.projectid}&_search=false&rows=15&page=${this.page}&sidx=&sord=asc&fuzzysearch=${this.search}`

    if (this.status == 'MY') {
      url += '&nw_resperson1=my'
    } else {
      let where = { toint_projectstatus_equal: this.status }
      url += '&where=' + encodeURI(JSON.stringify(where))
    }

    let loading = this.loadingCtrl.create({
      content: '正在加载中...', //数据加载中显示
      enableBackdropDismiss: true,
    });
    loading.present();
    this.httpService.get(url, null, async (result) => {
      loading.dismiss();
      if (result != null) {
        this.list = [...this.list, ...result.rows]
        this.maxPage = result.total
        // console.log(this.page, this.maxPage);
        // 初始化附件
        for (let index = 0; index < this.list.length; index++) {
          const item = this.list[index];
          item['initfiles'] = await this.initfiles(item)
        }
      }
    });
  }

  initfiles(item) {
    return new Promise(resolve => {
      this.httpService.getFiles('lmtagProject', item['prjid'], res => {
        if (res.code == 0) {
          resolve(res.data)
        }
        resolve([])
      })
    })
  }

  // 文件上传相关 START
  clickUpload(i) {
    this.index = i
    $('#file').click()
  }
  beforeUpload(e) {
    const file = e.target.files[0]
    $('#file')[0].value = ''
    console.log(e.target.files);
    let formdata = new FormData()
    formdata.append('file', file)
    formdata.append('busstype', 'lmtagProject')
    formdata.append('folderid', '')
    formdata.append('bussid', this.list[this.index]['prjid'])
    this.httpService.postFormData('/uploadify/upload', formdata, res => {
      console.log(res);
      if (res.error == 0) {
        this.toastCtrl.create({
          position: 'top',
          message: '上传成功',
          duration: 1200
        }).present();
        this.page = 1
        this.initlist();
      }
    })
  }
  // 文件上传相关 END

  // 文件大小格式化
  formatBytes(size) {
    var data = "";
    if (size < 0.1 * 1024) { //如果小于0.1KB转化成B
      data = size.toFixed(2) + "B";
    } else if (size < 0.1 * 1024 * 1024) {//如果小于0.1MB转化成KB
      data = (size / 1024).toFixed(2) + "KB";
    } else if (size < 0.1 * 1024 * 1024 * 1024) { //如果小于0.1GB转化成MB
      data = (size / (1024 * 1024)).toFixed(2) + "MB";
    } else { //其他转化成GB
      data = (size / (1024 * 1024 * 1024)).toFixed(2) + "GB";
    }
    var sizestr = data + "";
    var len = sizestr.indexOf("\.");
    var dec = sizestr.substring(len + 1, 2);
    if (dec == "00") {//当小数点后为00时 去掉小数部分
      return sizestr.substring(0, len) + sizestr.substring(len + 3, 2);
    }
    return sizestr;
  }

  removeFile(item) {
    this.httpService.deleteImage({ 'ids': item['id'] }, res => {
      if (res.code == 0) {
        this.toastCtrl.create({
          message: '删除文件成功',
          duration: 1200,
          position: 'top'
        }).present()
        this.page = 1
        this.initlist()
      }
    }, this.httpService.CONTENTTYPE_FORMURLENCODE)
  }

  // 下载相关函数
  checkPermissions() {
    this.permission.requestPermission(this.permission.PERMISSION.READ_EXTERNAL_STORAGE).then(res => { console.log(res) });
    this.permission.requestPermission(this.permission.PERMISSION.WRITE_EXTERNAL_STORAGE).then(res => { console.log(res) });
  }
  openFile(item) {
    if (item.filetype == 'gif' || item.filetype == 'jpg' || item.filetype == 'jpeg' || item.filetype == 'png' || item.filetype == 'bmp') {
      this.phoneDevice.showBigimage(item['url']);
    } else {
      let loading = this.loadingCtrl.create({
        content: '下载中，请稍等...',//数据加载中显示
        enableBackdropDismiss: true,
      });
      loading.present();
      let now = 0;
      var url = item['url'];
      let name = item['name'];
      if (this.platform.is('ios')) {
        this.filepath = this.file.dataDirectory + item['url'];
      } else {
        if (item['name'].substr(0, 1) == '#') {
          name = name.replace('#', ' ')
        }
        // this.file.externalApplicationStorageDirectory
        if (name.lastIndexOf('.') != -1) {
          this.filepath = this.file.externalApplicationStorageDirectory + name;
        } else {
          this.filepath = this.file.externalApplicationStorageDirectory + name + '.' + item['filetype'];
        }
      }
      let filetransfer: FileTransferObject = this.transfer.create();
      filetransfer.onProgress((progressEvent) => {
        if (progressEvent.lengthComputable) {
          now = progressEvent.loaded / progressEvent.total * 100;
        }
      })
      filetransfer.download(url, this.filepath, true).then((res) => {
        this.checkPermissions();
        loading.dismiss();
        this.fileopen.open(this.filepath, this.httpService.getFileMimeType(item['filetype'])).then((data) => {

        }, (err) => {
          // loading.dismiss();
          this.toastCtrl.create({
            position: 'top',
            message: '文件打开失败/无' + this.httpService.getFileMimeType(item['filetype']) + '查看器',
            duration: 2000
          }).present();
        })
      }, (err) => {
        loading.dismiss();
        this.toastCtrl.create({
          position: 'top',
          message: '文件下载失败',
          duration: 1200
        }).present();
      })
    }
  }
  // 下载相关函数 end
}



